
import XHeader from 'vux/src/components/x-header/index.vue'

export default{
components: {
XHeader
},
data() {
return {
id: "",
sjlym: "",
YLJGID: "",
info: {},
}
},
mounted() {
this.getParams();
},
methods: {

getParams() {
var routerParams = this.$route.params;
this.id = routerParams.id;
this.sjlym = routerParams.sjlym;
this.YLJGID = routerParams.YLJGID;
this.getInfo();
},

getInfo() {
this.$vux.loading.show({
text: '加载中...'
});
this.$ajax.post('',{
'MSH.1':'myreport',
'MSH.2':'yxbg',
'id':this.id,
'sjlym':this.sjlym,
'YLJGID':this.YLJGID,
'token':this.$root.loginInfo.token,
}).then(res => {
console.log(res);
this.$vux.loading.hide();
if (typeof res != 'undefined') {
this.info = res;
}
}).catch(error => {
console.log(error);
this.$vux.loading.hide();
});
},
},
}



